
body {  
    font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: rgba(97, 80, 252, 0.089);
}
.main{ 
    background-color: rgb(189, 189, 189);
    position: relative;
    left: 25%;
    top: 30px;
    width: 50%;
    border-radius: 10px;

}
.header{ 
    /* background-color: blue; */
    display: flex;    
    color: rgb(97, 80, 252);
    font-weight: bold;
    position: relative;
    top:40px;
    font-size: 19px;
    border-bottom: 2px whitesmoke;
}

#left-header { 
    flex: 65% 1; 
    padding-left: 15px;
    font-size: 25px;
}

#right-header { 
    flex: 35% 1;
    text-align: left;
}

.header i {
    position: relative;
    left: 75%;
    top: 30%;
}

.task-list{
    padding-left: 10px;
}

/*  */
#right-header i{ 
    color: rgb(97, 80, 252);
}







