
body {  
    font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: rgba(97, 80, 252, 0.089);
}
.main{ 
    background-color: rgb(189, 189, 189);
    position: relative;
    left: 25%;
    top: 30px;
    width: 50%;
    border-radius: 10px;

}
.header{ 
    /* background-color: blue; */
    display: flex;    
    color: rgb(97, 80, 252);
    font-weight: bold;
    position: relative;
    top:40px;
    font-size: 19px;
    border-bottom: 2px whitesmoke;
}

#left-header { 
    flex: 65%; 
    padding-left: 15px;
    font-size: 25px;
}

#right-header { 
    flex: 35%;
    text-align: left;
}

.header i {
    position: relative;
    left: 75%;
    top: 30%;
}

.task-list{
    padding-left: 10px;
}

/*  */
#right-header i{ 
    color: rgb(97, 80, 252);
}







#table td{ 
    font-size: 24px;
    padding-left: 10px;
    padding-bottom: 100px;
    padding-top: 10px;
    margin-bottom: 20px;
    display: inline;
    /* position: relative; */
    flex-direction: column;
}

#table{
    border-collapse: separate;
    border-spacing: 0px 15px;
}

#completebutton{ 
    position: absolute;
    left: 600px
}
#deletebutton{ 
    position: absolute;
    left: 520px; 
    border-radius: 20px;
}

.fa-times:active{
    color: rgb(189, 35, 35);
}

.btn{
    background-color: white;
    border: none;
    color: black;
    font-weight: bold;
    display: inline-block;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 5px;
    padding-bottom: 5px;
    border-radius: 5px;
}

#close_button{ 
    position: relative;
    left: 70%;
    top: 11%;
}


.form_prompt{ 
    background-color: rgba(97, 80, 252, 0.089);
}

.sortbutton{ 
    position: relative;
    
    background-color: white;
    border: none;
    color: black;
    font-weight: bold;
    display: inline-block;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 5px;
    padding-bottom: 5px;
    border-radius: 5px;


}

.buttonsort{ 
    position: fixed;
    top: 80px; 
    left: 43%;

}
